<template>
  <div>
    <h1>我的信息</h1>
    <div class="container">
      <!-- 信息编辑区 -->
      <el-card class="edit-info-card">
        <div slot="header" class="clearfix">
          <span>Edit Info</span>
        </div>
        <el-form ref="editformRef" :model="editform" label-width="80px">
          <el-form-item label="用户名称">
            <el-input v-model="editform.user_name"></el-input>
          </el-form-item>
          <el-form-item label="用户邮箱">
            <el-input v-model="editform.user_email"></el-input>
          </el-form-item>
          <el-form-item label="旧密码">
            <el-input :model="editform.user_password"></el-input>
          </el-form-item>
          <el-form-item label="新密码">
            <el-input :model="editform.checkpassword"></el-input>
          </el-form-item>
          <el-form-item label="个人简介">
            <el-input type="textarea" v-model="editform.user_info"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">更新信息</el-button>
            <el-button>取消</el-button>
          </el-form-item>
        </el-form>
      </el-card>
      <!-- 用户信息区 -->
      <el-card class="user-info-card">
        <img
          src="../../../assets/user/default.png"
          alt="用户头像"
          width="100px"
          height="100px"
        />
        <div>
          <h1>{{ this.$store.state.userinfo.user_id }}</h1>
          <h1>{{ this.$store.state.userinfo.user_name }}</h1>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      editform: {
        user_name: "",
        user_email: "",
        user_password: "",
        checkpassword: "",
        user_info: "",
      },
    };
  },
  created() {
    if (this.$store.state.userinfo) {
      let userinfo = this.$store.state.userinfo;
      this.editform.user_name = userinfo.user_name;
    } else {
      console.log(this.$store.state.userinfo);
    }
  },
  methods: {
    onSubmit() {
      console.log("submit!");
    },
  },
};
</script>

<style lang="less" scoped>
//  内容区容器
.container {
  display: flex;
}

.clearfix:before,
.clearfix:after {
  display: table;
}
.clearfix:after {
  clear: both;
}
.clearfix {
  border-left: 5px solid rgb(58, 152, 234);
  padding: 1px 25px;
}

// 用户信息编辑卡片
.edit-info-card {
  flex: 2;
  margin-right: 30px;
}

// 用户信息卡片
.user-info-card {
  flex: 1;
  text-align: center;
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
}

// 适配手机端
@media (max-width: 600px) {
  .container {
    flex-direction: column-reverse;
  }
  .edit-info-card {
    flex: 1;
    margin-right: 30px;
  }
  .user-info-card {
    flex: 1;
    margin-bottom: 30px;
    margin-right: 30px;
  }
}
</style>
